/* block wide width outside wrapper */
.wide-width {
  position: relative;
  width: 85vw;
  min-width: 100%;
  margin: auto calc(50% - 50vw);
  transform: translateX(calc(50vw - 50%));
}

/* block full width outside wrapper */
.full-width {
  position: relative;
  width: 100vw;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

/* cover with position absolute */
.cover-wrap {
  position: relative;
}
.cover-wrap .cover {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* image in wrap */
.image-wrap {
  position: relative;
  padding-top: 100%;
}
.image-wrap .image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  -o-object-fit: cover;
  object-fit: cover;
}

/*reset*/
.reset {
  background-color: transparent;
  padding: 0;
  border: 0;
  border-radius: 0;
  color: inherit;
  line-height: inherit;
  appearance: none;
}

/* highlight text */
.highlight {
  display: inline-block;
  padding: 2px 6px;
  border-radius: 6px;
}

/* animation */
/* use the following sequence of values for the animation property -
duration | timing-function | delay | iteration-count | direction | fill-mode | play-state | name */
/* mixins for animation */
/* fade-in */
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.fade-in {
  animation: 1s ease-in-out fade-in;
}
.fade-in .fast {
  animation: 0.3s ease-in-out fade-in;
}

/* fade up */
@keyframes fade-up {
  0% {
    opacity: 0.8;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-up {
  opacity: 0;
  animation: 0.3s ease-out forwards fade-up;
}

/*fade down*/
@keyframes fade-down {
  0% {
    opacity: 0.8;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-down {
  opacity: 0;
  animation: 0.3s ease-out forwards fade-down;
}

/*grow-x*/
@keyframes grow-x {
  to {
    width: 100%;
  }
}
.grow-x {
  width: 0%;
  animation: 0.3s ease 0.5s forwards grow-x;
}

/*shrink-x*/
@keyframes shrink-x {
  to {
    width: 0%;
  }
}
.shrink-x {
  animation: 0.3s ease-in-out 0.5s forwards shrink-x;
}

/*scale in*/
@keyframes scale-in {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.scale-in {
  animation: 0.3s cubic-bezier(0.2, 0, 0.13, 1.5) scale-in;
}

/*pulse*/
@keyframes pulse {
  0% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.pulse {
  animation: 2s linear infinite pulse;
}

/*pulse in*/
@keyframes pulse-in {
  0% {
    transform: scale3d(1, 1, 1);
  }
  50% {
    transform: scale3d(1.1, 1.1, 1.1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
.pulse-in {
  animation: 0.5s pulse-in;
}

/*rotate*/
@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
.rotate {
  animation: 1s linear infinite rotate;
}

/* Fence. For header, main, 2 aside & footer */
.fence {
  display: grid;
  grid-template-columns: 2fr;
